@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@600&display=swap);
body {
  color: #4f4f4f;
  margin: 0;
  font-family: 'Open Sans', sans-serif;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
    display: flex;
    flex-flow: column;
}

.content {
    margin: 10px 50px;
    display: flex;
    justify-content: space-around;
}

.container1,
.container2 {
    display: flex;
    flex-flow: column;
    flex-wrap: wrap;
}

.container2 {
    justify-content: center;
    align-items: center;
}

.error-container {
    z-index: -1;
    margin: auto auto;
    text-align: center;
    height: 0px;
    opacity: 0;
    transition: all 0.5s ease;
}

.show {
    position: relative;
    transform: translate(0px, 10px);
    opacity: 1;
}

.button-container {
    margin-top: 15px;
    margin-left: 50px;
    margin-right: 50px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.switch-icon {
    position: relative;
    top: 3px;
    left: 5px;
}

.here {
    padding: none;
    outline: none;
    border: none;
    height: 28px;
    border-radius: 5px;
    box-shadow: 2px 2px 3px #c8c8c8;
}

.here:active {
    box-shadow: 1px 1px 1px #c8c8c8;
}

.here span {
    margin-top: 4px;
    font-size: medium;
    color: #4f4f4f;
}

@media only screen and (max-width: 600px) {
    .content {
        flex-flow: column;
    }
}

.today-container {
    margin: 25px 0;
    padding: 0 15px;
    display: inline-block;
    background: #f2f2f2;
    border-radius: 32px;
    box-shadow: 3px 3px 5px #c8c8c8;
}

.today-description {
    text-transform: capitalize;
}

.svg {
    position: relative;
    top: 5px;
    display: inline-block;
    width: 20px;
}

.header {
    width: 100vw;
    height: 70px;
    background-color: #6fcf97;
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    font-size: xx-large;
}

.header-location {
    width: 33%;
    font-size: x-large;
}

.search-form {
    width: 33%;
    display: inline-block;
}

.search-form form {
    margin-right: 80px;
    display: inline-flex;
    align-items: center;
    float: right;
}

.search,
.submit {
    padding: 3px;
    height: 20px;
    outline: none;
    border: none;
    border-radius: 0;
}

.search {
    font: 'Op';
    border-top-left-radius: 5px;
    border-bottom-left-radius: 5px;
}
.submit {
    padding: 13px 1px;
    text-align: center;
    display: inline-flex;
    align-items: center;
    border-top-right-radius: 5px;
    border-bottom-right-radius: 5px;
}

.material-icons {
    font-size: small;
}

@media only screen and (max-width: 600px) {
    .header {
        font-size: x-large;
    }

    .header-location {
        width: 100px;
        font-size: small;
    }

    .search-form {
        white-space: nowrap;
        width: 100px;
    }

    .search-form form {
        margin: 0;
    }

    .search {
        height: 10px;
        width: 80%;
    }

    .submit {
        padding: 8px 1px;
        height: 10px;
        width: 20%;
    }
}

.forecast-container {
    background: #f2f2f2;
    margin: 25px 0px;
    padding: 0 15px;
    box-shadow: 3px 3px 5px #c8c8c8;
    border-radius: 32px;
    height: auto;
    max-width: 850px;
    width: auto;
    text-align: center;
}

.forecast-days-container {
    margin: 0px 0;
    padding: 0 15px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
}

.day-container {
    margin: 10px;
}

.title {
    margin: 0;
    padding-top: 10px;
}
@media only screen and (max-width: 600px) {
    .forecast-container {
        width: 65.5vw;
        height: auto;
    }
    .forecast-days-container {
        flex-flow: column;
    }

    .day-container {
        border-bottom: 1px solid #c1c1c1;
    }
}

